import React from "react";
import { Col, Row, Container } from "../Grid";

const Footer = () =>
  <Container>
	  <Row>
		<Col size="sm-12">
		  <hr />
		  <h5 className="text-center">NYT Scrubber 2017</h5>
		</Col>
	  </Row>
  </Container>;

export default Footer;


